import React from 'react';
import './HomeContent.css';

const homeContent = (props) => {
    return (
        <div className="rectangleHomeContent">
            <h2>Welcome to Destination Public School</h2>
            <strong><h3>Principal's Message</h3></strong>
            <img className="principalImage"
                 src={require("../../resources/images/principalImage.jpg")} 
                 alt="dummy">
            </img>
            <p>
                Destination Public School, constantly strives to raise 
                its standard and provide quality education to the underprivileged boys 
                and girls of the vicinity. Our goal therefore, is integral Education, to 
                which our school is primarily committed and towards which it directs 
                all talents, activities and energies.
            </p>
            <p>
                Life is a class-room, where-in we live to experiment, and learn 
                through the experiences we go-through whether they are positive or 
                negative.
            </p>
        </div>
    );
};

export default homeContent;