import React from 'react';
import './Navigation.css';

const navigation = (props) => {
    return (
      <div>
        <button className="tablink" onClick={ () => props.onclick('Home')}>Home</button>
        <button className="tablink" onClick={ () => props.onclick('About')}>About</button>
        <button className="tablink" onClick={ () => props.onclick('Faculty')}>Faculty</button>
        <button className="tablink" onClick={ () => props.onclick('Events')}>Events</button>
        <button className="tablink" onClick={ () => props.onclick('Contact')}>Contact</button>
      </div>
    );
};

export default navigation;