import React, {Component} from 'react';
import TopHeader from '../TopHeader/TopHeader';
import Navigation from '../Navigation/Navigation';
import HomeContent from '../Content/HomeContent';
import EventsContent from '../Content/EventsContent';
import ContactContent from '../Content/ContactContent';
import AboutContent from '../Content/AboutContent';
import Footer from '../Footer/Footer';

class Screen extends Component {
    state = {
        selectedTabContent: <HomeContent />,
        selectedTabName: 'Home'
    }

    render() {
        return (
            <header> 
                <TopHeader />
                <Navigation onclick={this.tabClickHandler}/>
                {this.state.selectedTabContent}
                <Footer />
            </header>
        );
    }

    tabClickHandler = (selectedTab) => {
        console.log(selectedTab);
        this.setState({selectedTabName: selectedTab});
        switch(selectedTab) {
            case 'Home':
                this.setState({selectedTabContent: <HomeContent />});
                break;
            case 'About':
                this.setState({selectedTabContent: <AboutContent />});
                break;
            case 'Events':
                this.setState({selectedTabContent: <EventsContent />});
                break;
            case 'Contact':
                this.setState({selectedTabContent: <ContactContent />});
                break;
            default:
                this.setState({selectedTabContent: <HomeContent />});
        }
    }

    
};

export default Screen;